@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
.item {
    height: 100%;
    align-self: center!important;
    display: flex!important;
}

header {
    display: flex!important;
    height: 56px;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px 10px 10px!important;
    width: 100%;
    position: fixed;
    background: white;
    z-index: 2020;
}


.logo1 {
    height: 50px;
    width: 50px;
    cursor: pointer;
    margin-right: auto;
    margin-left: 10px;
    
}

.header_nav {
    width: 85%;
    display: flex!important;
    justify-content: flex-end
}

.nav__links {
    list-style: none;
}

.nav__links li {
    display: inline-block;
    padding: 10px!important;
}

.login_button {
    margin-left:auto;
    padding: 9px 12px!important;
    background-color: gray;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    font-weight: 500;
    font-size: 8px;
    color: white;
    text-decoration: none
}
a {
    font-weight: 500;
    font-size: 8px;
    color: rgba(80,80,80,1);
    text-decoration: none 
}

.searchbar_header {
    margin: 5px;
    width: 50%;
    height: 32px;
    border: 0.7px solid rgba(80,80,80,0.7);
    display: flex!important;
}

.searchbox {
    width: calc(100%);
    place-content: center;
    border: none;
    padding-left: 4px;
    padding-right: 2px;
    line-height: 24px;
    
}
.search_button {
    width:30px;
    height: 30px;
    background-color: gray;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.card {
    transition: 0.3s;
    border-radius: 5px; /* 5px rounded corners */
    width: 356px;
    height: 307px;
}
  
  /* Add rounded corners to the top left and the top right corner of the image */
img {
    border-radius: 5px 5px 5px 5px;
    width : 100%;
    max-height : 200px!important;
    background: transparent;
}

.card:hover {
    box-shadow: 0 8px 16px 0 white;
}
  
  /* Add some padding inside the card container */
.container {
    
    position: relative;
}


.top-right {
  position: absolute;
  top: 8px;
  right: 8px;
  background: transparent;
}

.top-left {
  font-size: 13px;
  position: absolute;
  bottom: 8px;
  left: 8px;
  text-align: center;
  margin: auto;
  padding: 1px;
  color: white;
  background: rgba(80,80,80,20)
}

.title {
  width: 100%;
  height: 58px;
  padding: 8px 0 0 0;
  size: 22px;
  line-height: 22px;
}
.title a{
  color : rgba(22,22,22,1);
  overflow: hidden;
  text-overflow:ellipsis;
  -webkit-line-clamp: 2;
}

.contents {
  display: flex;
  bottom: 8px;
  width: 100%;
  height: 50px;
  font-size: 16px;
  align-items: center;

}

.title .text {
  font-size: 15px;
  font-weight: 600;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow:ellipsis;
  -webkit-line-clamp: 2;
}

.contents .creatorlogo {
  width: 40px;
  height: 40px!important;
  margin-left: 5px;
  margin-right: 5px;
}

.contents a {
  font-size: 14px;
  font-weight: 600;
  color: rgba(80,80,80,1);
  text-overflow:ellipsis;
  
  -webkit-line-clamp: 2;
}

.contents .platform {
  width: 40px;
  height: 40px;
  margin-left:auto;
  margin-right: 5px;
}

.contents .creatorlogo a img{
  width: 40px;
  height: 40px;

}


.contents .setting {
  
  margin-left: 5px;
  margin-right: 5px;
}

.contents .setting img {
  height: 20px;
  margin-top: 2px;
}


.popup_inner {
  width: 100px;
  height: 50px;
  position: absolute;
  background-color: white;
  margin-left: 240px;
  text-align: center;
  align-items: center;
  display: flex;
}

.popup_inner_item {
  margin: auto;
}

.adminList {
    padding-top: 50px;
    padding-left: 100px;
    padding-right: 100px;
    
}

.table {
    font-weight: 400;
    font-size: 12px;
    width: 100%;
    border: 2px solid #bcbcbc;
    background-color: whitesmoke!important;
}

.th {
    text-align: center;
    border: 2px solid #bcbcbc;
    background-color: whitesmoke!important;
}

.td {
    text-align: center;
    border: 2px solid #bcbcbc;
    background-color: whitesmoke!important;
}

.tbody {

    border: 2px solid #bcbcbc;
    background-color: whitesmoke!important;
}

.thead {

    border: 2px solid #bcbcbc;
    background-color: whitesmoke!important;
}

.tr {

    border: 1px solid #bcbcbc;
    background-color: whitesmoke!important;
}

