.login {
    display: inline-block;
    align-items: center;
    justify-content: center;
}

.form-control {
    font-size: 16px;
    transition: all 0.4s;
    box-shadow: none;
}
.form-control:focus {
    border-color: #bd59d4;
}
.form-control, .btn {
    border-radius: 50px;
    outline: none !important;
}
.signin-form {
    border-radius: 10px;
    width: 360px;
    margin: 0 auto;
    padding: 20% 0;
}
.signin-form form {
    border-radius: 10px;
    margin-bottom: 20px;
    background: white;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    padding: 40px;
    height: 250px;
}

.signin-form h2 {
    text-align: center;
    font-size: 34px;
    margin: 10px 0 15px;
}
.signin-form .hint-text {
    color: #999;
    text-align: center;
    margin-bottom: 20px;
}
.signin-form .form-group {
    margin-bottom: 20px;
}
.signin-form .btn {        
    font-size: 18px;
    line-height: 26px;        
    font-weight: bold;
    text-align: center;
}
.signin-form .small {
    font-size: 13px;
}
.signup-btn {
    text-align: center;
    border-color: #bd59d4;
    background-color: #bd59d4;
    transition: all 0.4s;
}
.signup-btn:hover {
    background: #4b2354;
    border-color: #4b2354;
    opacity: 0.8;
}
.or-seperator {
    margin: 50px 0 15px;
    text-align: center;
    border-top: 1px solid #e0e0e0;
}
.or-seperator b {
    padding: 0 10px;
    width: 40px;
    height: 40px;
    font-size: 16px;
    text-align: center;
    line-height: 40px;
    background: #fff;
    display: inline-block;
    border: 1px solid #e0e0e0;
    border-radius: 50%;
    position: relative;
    top: -22px;
    z-index: 1;
}

.login100-social-item {
    font-size: 25px;
    color: #3b5998;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: white;
    margin: 5px;
    box-shadow : 0 5px 20px 0px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
}

.login100-social-item img {
    width : 23px;
    vertical-align: middle;
    border-style: none;
}

#facebook img {
    width : 12px;
    vertical-align: middle;
    border-style: none;
}

.social-btn {
    text-align: center;
}

.social-btn .btn {
    display : flex;
    justify-content: center;
}	

.social-btn .btn:hover {
    opacity: 0.8;
}
.social-btn .btn-primary {
    background: #507cc0;
}
.social-btn .btn-info {
    background: #64ccf1;
}
.social-btn .btn-danger {
    background: #df4930;
}
.social-btn .btn i {
    font-size: 20px;
}

.mainBody_drawer_on {
    width: calc(100% - 250px);
    margin-left: auto;
    background: #FAFAFA;
    padding-top:50px;
    z-index: -1;
}

.mainBody_drawer_off {
    top: 50px!important;
    padding-top: 50px;
    background: #FAFAFA;
    z-index: -1;
}

