}

.airlist_container {
    width: 100%;
    display: inline-grid;
    padding: 40px;
    grid-template-rows: auto;
    grid-template-columns: repeat(auto-fit, 356px);
    grid-gap: 30px 20px;
    gap: 30px 20px;
    
    justify-content: space-evenly;
    
}

.item {
    display: flex;
    width: 356px;
    height: 307px;
    
}

.drawer {
    position: fixed;
    width: 240px;
    overflow-y: auto;
    top: 50px;
    bottom: 0px;
    margin-left: 10px;

}

.drawer_followerlist {
    padding-top: 10px;
